.default-overlay {
    position: relative;
    &:before {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        content: "";
        right: 0;
        bottom: 0;
    }
}


.btn-hover {
    a {
        position: relative;
        transition: all .5s ease-in-out 0s;
        &:hover {
            border: 1px solid $theme-color;
            color: #fff;
        }
        &::before, &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transition: all 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
            z-index: -1;
        }
        &::after {
            width: 0;
            left: auto;
            right: 0;
            background: $theme-color;
        }
        &:hover::after {
            width: 100%;
            left: 0;
            right: auto;
        }
    }
}



a.default-btn {
	font-size: 16px;
	color: #010101;
	display: inline-block;
	border: 1px solid #333;
	text-transform: uppercase;
	line-height: 1;
	padding: 19px 50px 21px;
}



/* slider img bounce */

@-webkit-keyframes bounce-img {
    0% {
        opacity: 1;
        transform: translateY(0px);
    }
    50% {
        opacity: 1;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}

@keyframes bounce-img {
    0% {
        opacity: 1;
        transform: translateY(0px);
    }
    50% {
        opacity: 1;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0px);
    }
}












