	z-index: 7;
	padding: 72px 0 40px 0px;
    @media #{$xx-layout}{
        width: 300px;
        padding:50px 0 30px 0px;
    }
    @media #{$xl-layout}{
        width: 300px;
        padding:50px 0 30px 0px;
    }
    @media #{$xl-layout}{
