            padding: 0px 0 0px 50px;
            margin-top: 26px;
            margin-bottom: 27px;
        }
        @media #{$xl-layout}{
            padding: 0px 0 0px 30px;
            margin-top: 26px;
            margin-bottom: 27px;
        }
        @media #{$lg-layout}{
            padding: 0px 0 0px 15px;
            margin-top: 30px;
            margin-bottom: 30px;
        }
        @media #{$md-layout}{
            padding: 0px 0 0px 30px;
