        }
        @media #{$xl-layout}{
            padding: 40px 63px 50px 63px;
        }
        @media #{$lg-layout}{
          padding: 40px 15px 50px 15px;  
        }
        @media #{$md-layout}{
          padding: 50px 15px 50px 30px;  
        }
        p{
            letter-spacing: .9px;
            color: #313131;
            margin: 0;
            text-align: center;
        }
    }
}

.home-sidebar-right {
	border-left: 1px solid #efefef;
	margin-left: 375px;
    @media #{$xx-layout}{
        margin-left: 300px;
    }
    @media #{$xl-layout}{
        margin-left: 280px;
    }
    @media #{$lg-layout}{
        margin-left: 250px;
    }
    @media #{$md-layout}{
        margin-left: 0px;
    }
    @media #{$xs-layout}{
        margin-left: 0px;
    }
}



.sidebar-menu {
    padding: 0px 0 0px 108px;
    @media #{$xx-layout}{
        padding: 0px 0 0px 50px;
    }
    @media #{$xl-layout}{
        padding: 0px 0 0px 30px;
    }
    @media #{$lg-layout}{
        padding: 0px 0 0px 15px;
    }
    @media #{$md-layout}{
        padding: 0px 0 0px 30px;
    }
    nav{
        ul {
            li{
                padding-bottom: 33px;
                position: relative;
                @media #{$xx-layout}{
                    padding-bottom: 20px;
                }
                @media #{$xl-layout}{
                    padding-bottom: 15px;
                }
                @media #{$lg-layout}{
                    padding-bottom: 10px;
                }
                @media #{$md-layout}{
                    padding-bottom: 12px;
                }
                &:last-child {
                    padding-bottom: 0;
                }
                ul.submenu {
                    background: #fff none repeat scroll 0 0;
                    box-shadow: 0 0 7px 0.5px rgba(0, 0, 0, 0.15);
                    display: block;
                    left: 100%;
                    padding: 20px 0px 22px;
                    position: absolute;
                    top: 0;
                    transform: rotateX(90deg);
                    transform-origin: center top 0;
                    transition: all 0.5s ease 0s;
                    visibility: hidden;
                    width: 190px;
                    z-index: 999;
                    @media #{$lg-layout} {
                        overflow-y: auto;
                        max-height: 250px;
                    }
                    li {
                        display: block;
                        padding: 0 20px;
                        margin-left: 0px;
                        a {
                            color: #333;
                            display: block;
                            font-size: 13px;
                            font-weight: 400;
                            line-height: 32px;
                            text-align: left;
                            text-transform: capitalize;
                            padding-top: 0px;
                            &:hover {
                                color: $theme-color;
                            }
                            &::before {
                                background: $theme-color none repeat scroll 0 0;
                                border-radius: 50%;
                                content: "";
                                height: 7px;
                                left: 15px;
                                opacity: 0;
                                position: absolute;
                                top: 13px;
                                transition: all 0.3s ease-in-out 0s;
                                width: 7px;
                                z-index: 999;
                            }
                        }
                        &:hover a::before {
                            opacity: 1;
                        }
                        &:hover a {
                            padding-left: 12px;
                        }
                    }
                }
                &:hover > ul.submenu {
                    transform: rotateX(0deg);
                    visibility: visible;
                }
                ul.mega-menu {
                    background-color: #fff;
                    display: block;
                    left: 100%;
                    padding: 40px 40px 0px 40px;
                    position: absolute;
                    text-align: left;
                    top: 0;
                    transform: rotateX(90deg);
                    transform-origin: center top 0;
                    transition: all 0.5s ease 0s;
                    visibility: hidden;
                    width: 700px;
                    z-index: 999;
                    box-shadow: 0 1px 7px -2px rgba(0, 0, 0, 0.3);
                    @media #{$xx-layout}{
                        top: -50px;
                    }
                    @media #{$xl-layout}{
                        top: -50px;
                    }
                    @media #{$lg-layout}{
                        top: -80px;
                    }
                    > li {
                        display: inline-block;
                        float: left;
                        margin-left: 0;
                        padding: 0;
                        width: 33.33%;
                        ul {
                            li.mega-menu-title {
                                float: inherit;
                                width: 100%;
                                a {
                                    color: #242424;
                                    margin: 0 0 10px;
                                    text-transform: uppercase;
                                    font-size: 14px;
                                    font-weight: 500;
                                    &::before {
                                        display: none
                                    }
                                }
                                &:hover a {
                                    padding-left: 0px;
                                }
                            }
                            li {
                                display: block;
                                float: inherit;
                                padding: 0px;
                                a {
                                    color: #333;
                                    display: block;
                                    font-size: 13px;
                                    font-weight: 400;
                                    line-height: 32px;
                                    text-align: left;
                                    text-transform: capitalize;
                                    padding-top: 0px;
                                    &:hover {
                                        color: $theme-color;
                                    }
                                    &::before {
                                        background: $theme-color none repeat scroll 0 0;
                                        border-radius: 50%;
                                        content: "";
                                        height: 7px;
                                        left: 0px;
                                        opacity: 0;
                                        position: absolute;
                                        top: 13px;
                                        transition: all 0.3s ease-in-out 0s;
                                        width: 7px;
                                        z-index: 999;
                                    }
                                }
                                &.mega-menu-img {
                                    &:hover a {
                                        padding-left: 0px;
                                    }
                                    a{
                                       &::before {
                                            display: none;
                                        } 
                                    }
                                }
                                &:hover a::before {
                                    opacity: 1;
                                }
                                &:hover a {
                                    padding-left: 12px;
                                }
                            }
                        }
                    }
                }
                &:hover > ul.mega-menu {
                    transform: rotateX(0deg);
                    visibility: visible;
                }
                a{
                    color: #000000;
                    font-size: 18px;
                    font-weight: 500;
                    letter-spacing: .8px;
                    text-transform: capitalize;
                    display: block;
                    @media #{$xl-layout}{
                        font-size: 16px;
                    }
                    @media #{$lg-layout}{
                        font-size: 16px;
                    }
                    @media #{$md-layout}{
                        font-size: 16px;
                    }
                    span {
                        float: right;
                        padding-right: 20px;
                    }
                }
                &:hover a{
                    color: $theme-color;
                }
            }
        }
    }
}


