/* home 7 */

.clickable-menu {
    margin-top: 35px;
    a {
        color: #fff;
        font-size: 30px;
        line-height: 1;
        display: inline-block;
        &:hover {
            color: $theme-color;
        }
    }
    @media #{$md-layout}{
		margin-top: -3px;
	}
    @media #{$xs-layout}{
		margin-top: -3px;
	}
}
.clickable-mainmenu {
	background: rgba(255, 255, 255, 1) none repeat scroll 0 0;
	color: #353535;
	height: 100vh;
    overflow-y: auto;
	padding: 130px 35px 40px;
	position: fixed;
	left: 0;
	top: 0;
	transform: translateX(-110%);
	transition: transform 0.5s ease-in-out 0s;
	width: 300px;
	z-index: 9999;
    &.inside {
        transform: translateX(0px);
	    z-index: 9999;
    }
    @media #{$xs-layout}{
		padding: 100px 35px 40px;
	}
    .clickable-mainmenu-icon {
        button {
            background: transparent none repeat scroll 0 0;
            border: medium none;
            color: #000;
            cursor: pointer;
            font-size: 50px;
            padding: 0;
            position: absolute;
            left: 20px;
            top: 20px;
            transition: all 0.3s ease 0s;
            &:hover {
                color: $theme-color;
            }
        }
    }
    .side-logo {
        margin-bottom: 50px;
        @media #{$xs-layout}{
            margin-bottom: 40px;
        }
    }
    .clickable-menu-style {
        background: transparent;
        ul {
            li {
                a {
                    color: #000;
                    font-size: 16px;
                    text-transform: capitalize;
                    padding: 0 0 20px;
                    &:hover {
                        background: transparent;
                        color: $theme-color;
                    }
                }
            }
        }
    }
    .side-social {
        margin-top: 50px;
        ul {
            li {
                display: inline-block;
                margin: 0 30px 0 0;
                position: relative;
                &:last-child {
                    margin-right: 0;
                }
                &:before {
                    position: absolute;
                    right: -20px;
                    top: 50%;
                    background: #999;
                    height: 1px;
                    width: 10px;
                    content: "";
                    transform: translateY(-50%);
                }
                &:last-child:before {
                    display: none;
                }
                a{
                    font-size: 18px;
                    &.facebook {
                        color: #3b5999;
                        &:hover {
                            color: #333;
                        }
                    }
                    &.dribbble {
                        color: #ea4c89;
                        &:hover {
                            color: #333;
                        }
                    }
                    &.pinterest {
                        color: #bd081c;
                        &:hover {
                            color: #333;
                        }
                    }
                    &.twitter {
                        color: #55acee;
                        &:hover {
                            color: #333;
                        }
                    }
                    &.linkedin {
                        color: #0077B5;
                        &:hover {
                            color: #333;
                        }
                    }
                }
            }
        }
    }
}



